class Sankeys extends React.PureComponent {
  static propTypes = {
    nodes: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
    links: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
  };

  render() {
    const { nodes, links, ...props } = this.props;

    return (
      <Wrapper {...props}>
        <Sankey
          nodes={nodes}
          links={links}
          width={1140}
          height={2400}
        />
      </Wrapper>
    );
  }
}

